.card{
    background-color: rgba(255, 255, 255, 0.219);
    display: inline-block;
    border-radius: 15px;
    margin: 20px;
    max-width: 250px;
    height: 250px;
    flex: 1;
    padding: 16px; 
    position: relative;   
    -webkit-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.06);
    -moz-box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.06);
    box-shadow: 10px 10px 5px 0px rgba(0,0,0,0.06);

    -webkit-transition: all 1s linear;
    -moz-transition: all 1s linear;
    -ms-transition: all 1s linear;
    -o-transition: all 1s linear;
    transition: all 1s linear;
}
.card-info{
    text-align: center;
    margin-top:20px;
    position: absolute;
    left: 50%;
    bottom: 0;
    transform: translate(-50%)
}

.card-img{

    display: block;
    margin: 0 auto;
    margin-top: 20px;
    width: 200px;
}

.card:hover{
    -moz-transform: scale(1.1) translateX(1px);
-webkit-transform: scale(1.1) translateX(1px);
-o-transform: scale(1.1) translateX(1px);
-ms-transform: scale(1.1) translateX(1px);
transform: scale(1.1) translateX(1px);
}